import React, { useContext, useState } from "react";
import { UserContext } from "../providers/UserProvider";
import { pemFirestore } from "../firebase/config";
import { useParams, useLocation} from "react-router-dom";


export default function NewAspect({ showAspectList, showNewAspectForm, aspectNameList}) {

    const [inputList, setInputList] = useState([{ featureName: null, featureValue1: null, featureValue2: null }]);
    const [aspectName, setAspectName] = useState(['name']);
    const {userId, dceId } = useParams();

    const user = useContext(UserContext);
    const uid = user.uid;

 

//helper for aspectNotNull check
  const existsName = (name => aspectName[0].aspectName===name);

//Check if aspect name is filled in before save. Also check if aspect name is unique
  const aspectNotNull = ()=>{
    if(aspectName[0].aspectName==="" || aspectNameList.some(existsName)){
      alert("Fill in a unique aspect name");
      return false;
    }
   
    return true;
  }

  //Save aspect if all fields are filled in
    const saveAspect = (e)=>{
        e.preventDefault();
        if(aspectNotNull()){
        let aspectColl = pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList');
        aspectColl.add( {aspectName: aspectName[0]})
        .then((docRef)=>{
              console.log("Document written with ID: ", docRef.id);
              aspectColl.doc(docRef.id).collection('Combis').doc('0').set({nr: 0, groupValue: "", filters: []})
        })
        .catch((error) => {
            console.error("Error adding document: ", error);
        });
        showAspectList();
        showNewAspectForm();
    }
  }

    // handle name input change
    const handleNameInputChange = (e) => {
        const name = e.target.value;
        const nameList = [...aspectName];
        nameList[0]=name;
        setAspectName(nameList);
    }



 
  // deletes aspect from database and refreshes list
  const handleRemoveClick = index => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
    showAspectList();
  };
 
 


 

  //html to show in browser
  return (
        <div>
            <form className="form-inline">
                <div className="form-floating">
                    <input type="text" className="form-control" name="aspectName"  placeholder="Aspect name" id="aspectName" 
                    required onChange={handleNameInputChange}></input>
                    <label htmlFor="aspectName" className="block">Name: </label>
                    </div>
{/*                   
                {inputList.map((x, i) => {
        return (
          <div className=" row">
              <div className="col-xs-12 col-sm-9 form-floating">
            <input
            className="form-control "
              name="featureName"
              required
              id={i}
   placeholder="Enter feature name"
              value={x.featureName}
              onChange={e => handleInputChange(e, i)}
            />             
            <label htmlFor={i} className="">Feature name: </label>
            </div>
            <div className="col col-xs-1">
            {inputList.length !== 1 && <button
                className="btn btn-sm btn-outline-success"
                onClick={() => handleRemoveClick(i)}><i class="material-icons mat-icon">delete_forever</i></button>}
              </div>

              <div className="col-xs-12 col-sm-9 form-floating">
              <input
            className="form-control "
              name="featureValue1"
              required
              id={i}
              placeholder="Enter feature value 1"
              value={x.featureValue1}
              onChange={e => handleInputChange(e, i)}
            />             
            <label htmlFor={i} className="">Feature value 1: </label>
            </div>
              <div className="col-xs-12 col-sm-9 form-floating">
              <input
            className="form-control "
              name="featureValue2"
              required
              id={i}
              placeholder="Enter feature value 2"
              value={x.featureValue2}
              onChange={e => handleInputChange(e, i)}
            />             
            <label htmlFor={i} className="">Feature value 2: </label>
            </div>
              <div>
              {inputList.length - 1 === i && <button className="btn btn-sm btn-outline-info" onClick={handleAddClick}>Add another feature</button>}</div>
           </div>
        );
      })}
   */}
     
   
      <button className="btn btn-sm btn-outline-success" onClick={saveAspect} >Save aspect</button>
          </form>
        </div>
    )
}
