import  React, { useEffect, useState, useContext } from "react";
import { Link, withRouter } from "react-router-dom";
import {pemAuth} from "../firebase/config";
import { UserContext } from "../providers/UserProvider";



function Navigation(props) {
    const CUser = useContext(UserContext);

    const [user, setUser] = useState(CUser);


    useEffect(() => {
        setUser(pemAuth.currentUser);
        })

    
 

  return (
    <div className="navigation">
      <nav class="navbar navbar-expand navbar-dark bg-dark">
        <div class="container">
          <Link class="navbar-brand" to="/">
            PEM     
                 </Link>

          <div>
            <ul class="navbar-nav ml-auto">
              <li
                class={`nav-item  ${
                  props.location.pathname === "/" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/">
                  Develop
                </Link>
              </li>
              <li
                class={`nav-item  ${
                  props.location.pathname === "/dces" ? "active" : ""
                }`}
              >
                {/* <Link class="nav-link" to="/dces"> */}
                <Link class="nav-link" to="/dces">

                  DCE's
                </Link>
              </li>
              <li
                class={`nav-item  ${
                  props.location.pathname === "/results" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/results">
                  Results
                </Link>
              </li>
              {user&&console.log("user in signout btn is: "+ JSON.stringify(user.displayName))}
              <li className="nav-item">
          <Link to="/" className="nav-link" onClick = {() => {pemAuth.signOut()}}>Sign out</Link>
        </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default withRouter(Navigation);