import React, {useState, useEffect} from 'react';
import { useParams, useLocation} from "react-router-dom";
import useFirestoreLoadResult from '../hooks/useFirestoreLoadResults';
// import '../index.css';


export default function AspectEdit({aspectNameEdit, aspectFilters, saveFilters, chosenFeatures, setChosenFeatures, handleAddCombi, handleDeleteCombi, aspectsObjs, groupValues, setGroupValues}) {

    // const [feature, setFeature] = useState('q');
    // const [fValue, setFValue] = useState('h');
    // const [aspects, setAspects] = useState([]);
    const [fCounter, setFCounter] = useState(1);
    const [formValue1, setFormValue1] = useState(null)
    const [formValue2, setFormValue2] = useState(null)
    const [html, setHtml] = useState([]);
    const [fromDb, setFromDb] = useState([]);
    const [resultHtml, setResultHtml] = useState([])
    const {userId, dceId } = useParams();
    const location = useLocation();
    const dceFeatures = location.dceFeatures;
    const groupFeature = location.groupFeature;
    const {results} = useFirestoreLoadResult(userId,dceId);
    console.log("props from location: "+JSON.stringify(location));
    console.log("aspectNameEdit: "+ JSON.stringify(aspectNameEdit));
    // console.log("props: "+ JSON.stringify(props));
    console.log("aspecteFilters: "+JSON.stringify(aspectFilters))




    useEffect(() => {
        let tempResult = [];
        aspectsObjs.forEach((aspect)=>{
            let aspectName = aspect.aspectName;
            let oddsFavor = [];
            let oddsAgainst = [];
            console.log('aspectObj: '+JSON.stringify(aspect));
            // aspect.combis.forEach(combi=>{
                let nr = aspect.combis.nr;

                aspect.combis.filters.forEach(filter=>{
                    const checkValue = (result=>{
                        console.log('forEach filter: '+ JSON.stringify(filter))
                    console.log('result in forEach: '+ JSON.stringify(result))
                    return(filter.fValue===result[filter.fName]&&result.hasOwnProperty(filter.fName))
                    })
                    let tempFavors = results.filter(checkValue);
                    tempFavors.forEach(favor=>{
                        if(oddsFavor.indexOf(favor)===-1){
                            oddsFavor.push(favor);
                        }
                    })
                })

            
                console.log('oddsFavor: '+JSON.stringify(oddsFavor))
                tempResult.push(
                <div>
                    Aspect: {aspectName} 
                    Nr: {nr} 
                    Length: {oddsFavor.length} 
                    {JSON.stringify(oddsFavor)}
                </div>)
                console.log('resultHtml: '+JSON.stringify(tempResult))
                setResultHtml(tempResult);
            // })
            })
                return 
    }, [aspectsObjs])

    
    const handleFName = (e)=>{
        e.preventDefault();
        setFormValue1(null);
        console.log("SetFormValue null");
        const { value, id} = e.target;
        const cIndex = e.target.dataset.id;
        console.log("handleChosen triggered, target:"+JSON.stringify(e.target.dataset));
        console.log("value is: "+JSON.stringify(value));
        console.log("chosenFeatures before copy: "+JSON.stringify(chosenFeatures));
        console.log("aspectFilter before copy: "+JSON.stringify(aspectFilters));
        const chosenList = [...chosenFeatures];
        // chosenList[0]['fName'] = value;
        chosenList[cIndex][0]={fName: value};
        // chosenList.push({fName: value});
        console.log("chosenList: "+ JSON.stringify(chosenList));
        setChosenFeatures(chosenList);
    }

    const handleFValue = (e) =>{
        e.preventDefault();
        setFormValue1(null);
        setFormValue2(null);
        const {value, id } = e.target;
        const cIndex = e.target.dataset.id;
        console.log("handleFValue triggered, cIndex = "+cIndex);
        const chosenList = [...chosenFeatures];
        console.log("chosenList: "+ JSON.stringify(chosenList));
        chosenList[cIndex][0]['fValue'] = value;
        // chosenList[0]={fValue: value};
        console.log("chosenList: "+ JSON.stringify(chosenList));
        setChosenFeatures(chosenList);
    }
    
    const handleGroupValue = (e)=>{
        e.preventDefault();
        const {value, id } = e.target;
        const cIndex = e.target.dataset.id;
        let tempGValues = [...groupValues]
        tempGValues[cIndex]=value
        setGroupValues(tempGValues);
        console.log('handleGroup value: '+value);
        console.log('Group values: '+JSON.stringify(tempGValues));
    }
 

const handleAddFeatureClick = ()=>{
    let tempCounter = fCounter;
    setFCounter(tempCounter+1);
}

const handleSave = (e)=>{
    e.preventDefault();
    const cIndex = e.target.dataset.id;
    console.log('handleSave data-id: '+JSON.stringify(e.target.dataset.id))
    console.log('chosenFeatures at start of handleSave: '+JSON.stringify(chosenFeatures));
    console.log('chosenFeatues[cIndex] at start of handleSave: '+JSON.stringify(chosenFeatures[cIndex]));
    let tempList = [...aspectFilters[cIndex],...chosenFeatures[cIndex]];
    let tempChosen = [...chosenFeatures];
    tempChosen[cIndex] = [];
    console.log('group values: '+JSON.stringify(groupValues))
    if (groupValues[cIndex].length===0){
        alert("Select a value for the Group feature before saving")
    } else{
    saveFilters(cIndex, tempList, tempChosen, groupValues);
    console.log("tempList in save: "+JSON.stringify(tempList))
    console.log("tempChosen in save: "+JSON.stringify(tempChosen))
    // setChosenFeatures(tempChosen);
    setFormValue1("");
    console.log("SetFormValue blanc");
    setFormValue2("");
    }
}

const handleDelete = (e, index, cIndex)=>{
    e.preventDefault();
    let tempCIndex = cIndex.toString();
    console.log("delete filter with id "+index);
    console.log("delete filter with cIndex "+tempCIndex);
    let tempFilters = [...aspectFilters[tempCIndex]];
    console.log('tempFilters voor splice: '+JSON.stringify(tempFilters))
    let emptyChosen = [];
    aspectFilters.map(x=>{emptyChosen.push([])});
    tempFilters.splice(index, 1);
    console.log('tempFilters na splice: '+JSON.stringify(tempFilters))
    console.log('emptyChosen na splice: '+JSON.stringify(emptyChosen))
    saveFilters(tempCIndex, tempFilters, emptyChosen, groupValues);
}



useEffect(() => {
    console.log("chosenFeatures in useEffect: "+JSON.stringify(chosenFeatures));
    console.log("aspectFilters in useEffect: "+JSON.stringify(aspectFilters))
    setFormValue1("");
    // console.log("SetFormValue blanc");
    setFormValue2("");

    let tempFromDB =[];
    aspectFilters.map((combi, cIndex)=>{
        tempFromDB.push(
           <div> {(cIndex!==0)&&(<h5 className="my-5">
           <button onClick={(e)=>handleDeleteCombi(e,cIndex)} className="btn  btn-outline-success btn-sm mx-2"  > <i class="material-icons mat-icon">delete_forever</i></button>
           combined with: 
           </h5>
           )}
           <h5>Select value for group feature - {groupFeature}</h5>
           <label htmlFor="groupVal">Group value
           {console.log('dceFeatures and groupFeature in select: '+JSON.stringify(dceFeatures)+JSON.stringify(groupFeature))}
           {console.log('groupValues: '+JSON.stringify(groupValues))}
<select name="groupVal" id="" className="form-select form-select-sm col-md-3  mb-3" defaultValue="" value={groupValues[cIndex]} data-id={cIndex} onChange={handleGroupValue}>
<option value="" disabled></option>
{dceFeatures&&<option value={dceFeatures.find(x=>x.featureName===groupFeature).featureValue1}>{dceFeatures.find(x=>x.featureName===groupFeature).featureValue1}</option>}
{dceFeatures&&<option value={dceFeatures.find(x=>x.featureName===groupFeature).featureValue2}>{dceFeatures.find(x=>x.featureName===groupFeature).featureValue2}</option>}
</select>
              </label>
          <h5>Saved features and values</h5>

           </div>
        )
        combi.map((filter, index)=>{
        console.log("filter is: "+JSON.stringify(filter));
        tempFromDB.push(
        <div className="my-3"><div className="savedFeatures my-2 mx-3 px-5 py-1 p-3 border rounded">{filter.fName}</div><span className="savedFeatures mx-3 px-5 py-1 border rounded">{filter.fValue}</span>
            <span>
               <button onClick={(e)=>handleDelete(e,index, cIndex)} className="btn  btn-outline-success btn-sm mx-2"  > <i class="material-icons mat-icon">delete_forever</i></button>
               </span>
          </div>
    ) // end of fromDb
}) // end of combi 
{          // add filter button - fromDb pt2
}
tempFromDB.push(
    <div>
          <span>
            <form>
                {console.log('groupFeature: '+groupFeature)}
                {console.log('dceFeatures: '+JSON.stringify(dceFeatures))}
              <label htmlFor='fNames' className="col-md-3 col-form-label">Add filter feature
                  <select className="form-select form-select-sm col-md-3  mb-3" defaultValue="" value={formValue1}  data-id={cIndex} id={0} onChange={handleFName}>
                    {/* <option value="" disabled></option> */}
                      {dceFeatures&&dceFeatures.map((feature,ind) =>{
                          console.log("ind is: "+ind);
                          let optionHtml = feature.featureName;
                          let disabled = "";
                          if (feature.featureName===groupFeature){
                              optionHtml = "";
                              disabled = "disabled"
                          }
                          return (
                            <option value={optionHtml} disabled={disabled}>{optionHtml} </option>
                          )
                      })}
                  </select>
              </label>
              </form>
              {dceFeatures&&<form>
              <label htmlFor="fValue">Filter value
<select name="fValue" id="" className="form-select form-select-sm col-md-3  mb-3" defaultValue="" value={formValue2} data-id={cIndex} onChange={handleFValue}>
<option value="" disabled></option>
{console.log("chosenFeatures: "+JSON.stringify(chosenFeatures))}
{chosenFeatures[cIndex]&& console.log("chosenFeatures[cIndex][0]: "+JSON.stringify(chosenFeatures[cIndex]))}
{chosenFeatures[cIndex][0]&&<option value={dceFeatures.find(x=>x.featureName===chosenFeatures[cIndex][0]["fName"]).featureValue1}>{dceFeatures.find(x=>x.featureName===chosenFeatures[cIndex][0]["fName"]).featureValue1}</option>}
{chosenFeatures[cIndex][0]&&<option value={dceFeatures.find(x=>x.featureName===chosenFeatures[cIndex][0]["fName"]).featureValue2}>{dceFeatures.find(x=>x.featureName===chosenFeatures[cIndex][0]["fName"]).featureValue2}</option>}
</select>
              </label>
              </form>}
              </span>
              <button data-id={cIndex} className="btn btn-sm btn-outline-info mb-3" onClick={handleSave}>save filter feature and group value</button>

              </div>
    ) // end of fromDb pt2
   
}) // end of aspect
    // }
    setFromDb(tempFromDB);
    // setChosenFeatures(aspectFilters);
    return
    
}, [chosenFeatures, groupValues])
   

useEffect(() => {
    console.log("Rerender choose html");
    console.log("chosenFeatures in choose html: "+JSON.stringify(chosenFeatures));
    setFormValue2("");
    let tempHtml = [];
    if(aspectNameEdit!=='(Click on EDIT in Aspect list)'){
        tempHtml.push(
            <div>
            <form>
              <label htmlFor='fNames' className="col-md-3 col-form-label">Add filter feature
                  <select className="form-select form-select-sm col-md-3  mb-3" defaultValue="" value={formValue1} data-id={0} id={chosenFeatures.length-1} onChange={handleFName}>
                    <option value="" disabled></option>
                      {dceFeatures&&dceFeatures.map((feature,ind) =>{
                          console.log("ind is: "+ind)
                          return (
                            <option value={feature.featureName}>{feature.featureName} </option>
                          )
                      })}
                  </select>
              </label>
              </form>
              {dceFeatures&&<form>
              <label htmlFor="fValue">Filter value
<select name="fValue" id="" className="form-select form-select-sm col-md-3  mb-3" defaultValue="" value={formValue2} data-id={0} onChange={handleFValue}>
<option value="" disabled></option>
{console.log("i="+chosenFeatures.length-1)}
{console.log("chosenFeatures: "+JSON.stringify(chosenFeatures))}
{chosenFeatures[0][0]&&<option value={dceFeatures.find(x=>x.featureName===chosenFeatures[0][0]["fName"]).featureValue1}>{dceFeatures.find(x=>x.featureName===chosenFeatures[0][0]["fName"]).featureValue1}</option>}
{chosenFeatures[0][0]&&<option value={dceFeatures.find(x=>x.featureName===chosenFeatures[0][0]["fName"]).featureValue2}>{dceFeatures.find(x=>x.featureName===chosenFeatures[0][0]["fName"]).featureValue2}</option>}
</select>
              </label>
              </form>}
              <button data-id={'0'} className="btn btn-sm btn-outline-info mb-3" onClick={handleSave}>save filter feature</button>

              </div>
        )}
        setHtml(tempHtml);

    return 
}, [chosenFeatures])

let addCombiButton = [];
   if(aspectNameEdit!=='(Click on EDIT in Aspect list)'){
       addCombiButton.push(
        <button className="btn btn-sm btn-outline-info my-4" onClick={handleAddCombi}>add combination feature</button>
        )
}

    return (
        <div>
            <h4>Define aspect for {aspectNameEdit}</h4>
            Results: {JSON.stringify(results)}
            {/* filter results: {JSON.stringify(results.filter(checkValue, featToCheck))}<br/> */}
filter results: {resultHtml}
            dceFeatures: {dceFeatures&&JSON.stringify(dceFeatures)}

     
              {fromDb}
              {fromDb.length===0&&html}
              {/* <button className="btn btn-sm btn-outline-info" onClick={handleSave}>save filter feature</button> */}
              {()=>{ if(aspectNameEdit!=='(Click on EDIT in Aspect list)'){return
              <button className="btn btn-sm btn-outline-info my-4" onClick={handleAddCombi}>add combination feature</button>
              }}}
              {fromDb.length>2&&addCombiButton}
        </div>
    )
}
