import React from "react";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import Nav from "./Nav";
import ResearchApp from "../ResearchApp";
import ResearchDce from "../ResearchDce";
import Dces from "./Dces";
import App from "../App";
import AspectEdit from "./AspectEdit";
import AspectList from "./AspectList";

function MainApp() {
  return (
    <div className="App">
      <Router>
        <Nav />
        {/* <Switch> */}
          <Route path="/" exact component={() => <ResearchApp />} />
          <Route path="/dces" exact component={() => <ResearchDce />} />
          <Route path="/app/:userId/:dceId" exact component={()=> <App/>} />
          {/* <Route path="/AspectEdit/:userId/:dceId" exact component={()=> <AspectEdit/>} /> */}
          <Route path="/aspectList/:userId/:dceId" exact component={()=> <AspectList/>} />
          <Route path="/results" exact component={() => <Dces />} />
        {/* </Switch> */}
      </Router>
    </div>
  );
}

export default MainApp;