import OptieB from './OptieB';
import OptieA from "./OptieA";

const PcView = ({handleClick, qIndex, sc1, sc2 }) => {
    return ( 
        <div className="pc-view">
	<h4>Make a choice between the 2 options</h4>

<div className="pc-container">
	<div className="option-container">
	<OptieA handleClick={handleClick} sc1={sc1} sc2={sc2}/>

	</div>
	<div className="option-container">
	<OptieB handleClick={handleClick} sc2={sc2} sc1={sc1}/>

        </div>
        </div>
        </div>
     );
}
 
export default PcView;